<script setup>
import { ref, onMounted } from "vue";
import AppMenuItem from "./AppMenuItem.vue";
import axios from "axios";
import { useRoute, useRouter } from "vue-router";
const route = useRoute();
const router = useRouter();
let display = ref(false);

let tokenjwt = localStorage.getItem("token");

const model = ref([
  {
    label: "accueil",
    items: [
      {
        label: "tableau de bord ",
        icon: "fas fa-tachometer-alt-slow",
        to: "/view/Dashboard",
      },
      { label: "Voir Residences", icon: "fas fa-city", to: `/view/residence` },
    ],
  },
  {
    label: "Residences",
    items: [],
  },
  {
    label: "Location",

    items: [
      {
        label: "nouvelle location",
        icon: "fas fa-clinic-medical",
        items: [
          {
            label: "Location Appartements ",
            icon: "fas fa-house",
            to: `/Notrentlocation/AppF/`,
          },
          {
            label: "Location Lit ",
            icon: "fas fa-procedures",
            to: `/Notrentlocation/LitApp/`,
          },
          {
            label: "Location Local Commercial",
            icon: "fas fa-store",
            to: `/Notrentlocation/localcommercial/`,
          },
        ],
      },
      {
        label: "Gestion Location",
        icon: "fas fa-house-day",
        items: [
          { label: "Fin Location", icon: "fas fa-house-leave" },
          { label: "Prolongation Location", icon: "fas fa-house-return" },
          { label: "Credit Payée", icon: "fas fa-eye" },
        ],
        goto:"/view/dashboard/GestionLoc"
      },
      {
        label: "Paiement",
        icon: "fas fa-money-check",
        items: [
          { label: "Location / Cautionnement", icon: "fas fa-warehouse-alt" },
          { label: "Steg / Sonede", icon: "fas fa-lightbulb-on" },
        ],
        goto:'/view/dashboard/paiement'
      },
    ],
  },
  {
      label: 'Depense',
      items: [
          {
              label: 'Ajout Depense',
              icon: 'fas fa-plus-circle',
              items:[
                  { label: 'Maintenance Residence', icon: 'fas fa-tools',  },
                  { label: 'Autre Depense ', icon: 'fas fa-hand-holding-usd',  },
                  { label: 'Charge Fixe ', icon: 'fas fa-hand-holding-usd',  },

              ],
              goto:'/depenses/client/'
          },
          {
              label: 'Ajout Fournisseur',
              icon: 'fas fa-parachute-box',
              
              to: `/depenses/Fournisseur/`,
              
          },
      ],
  },
]);

axios
  .get(`${process.env.VUE_APP_HOST_BACKEND}/residence/navbar/name`, {
    headers: {
      Authorization: tokenjwt,
    },
  })
  .then((response) => {
    if (response) {
      const ResidenceNames = response.data.ResidenceNames;
      const ResidenceIds = response.data.ResidenceIds;
      const adminlvl=response.data.adminlvl;
      if (adminlvl==1){
        ResidenceNames.forEach((element, index) => {
          const id = ResidenceIds[index];
          model.value[1].items.push({
            label: element,
            icon: "fas fa-city",
            items: [
              {
                label: "Voir Blocs",
                icon: "fas fa-building",
                to: `/biens/blocs/${id}`,
              },
              {
                label: "Voir Etages",
                icon: "fas fa-window-frame-open",
                to: `/biens/etages/${id}`,
              },
              {
                label: "Voir Appartement",
                icon: "fas fa-house-user",
                to: `/biens/appartement/${id}`,
              },
              {
                label: "Voir Lits",
                icon: "fas fa-bed-bunk",
                to: `/biens/lits/${id}`,
              },
              {
                label: "Voir Local Commercial",
                icon: "fas fa-store-alt",
                to: `/biens/localc/${id}`,
              },
            ],
          });
        });
      } else {
        // If adminlvl is not 1, remove the "Residences" section
        model.value.splice(1, 1);
      }
      
    }
  })
  .catch((error) => {
    model.value[1].items.push({
      label: "PAS DE RESIDENCE",
      icon: "fas fa-city",
    });
  });
onMounted(() => {
  const { hash } = route;
  if (hash === "#Payment") {
    display.value = true;
  }
});
</script>

<template>
  <ul class="layout-menu">
    <template v-for="(item, i) in model" :key="item">
      <app-menu-item
        v-if="!item.separator"
        :item="item"
        :index="i"
      ></app-menu-item>
      <li v-if="item.separator" class="menu-separator"></li>
    </template>
  </ul>
</template>

