<script setup>
import { ref, computed, onMounted, onBeforeUnmount } from 'vue';
import { useLayout } from './composables/layout';
import { useRouter } from 'vue-router';
import axios from 'axios';
const { layoutConfig, onMenuToggle } = useLayout();

const outsideClickListener = ref(null);
const topbarMenuActive = ref(false);
const router = useRouter();

const onSettingsClick = () => {
    router.push('/user/Dashboard');
};




const onTopBarMenuButton = () => {
    topbarMenuActive.value = !topbarMenuActive.value;
};

const topbarMenuClasses = computed(() => {
    return {
        'layout-topbar-menu-mobile-active': topbarMenuActive.value
    };
});

const bindOutsideClickListener = () => {
    if (!outsideClickListener.value) {
        outsideClickListener.value = (event) => {
            if (isOutsideClicked(event)) {
                topbarMenuActive.value = false;
            }
        };
        document.addEventListener('click', outsideClickListener.value);
    }
};
const unbindOutsideClickListener = () => {
    if (outsideClickListener.value) {
        document.removeEventListener('click', outsideClickListener);
        outsideClickListener.value = null;
    }
};
const isOutsideClicked = (event) => {
    if (!topbarMenuActive.value) return;

    const sidebarEl = document.querySelector('.layout-topbar-menu');
    const topbarEl = document.querySelector('.layout-topbar-menu-button');

    return !(sidebarEl.isSameNode(event.target) || sidebarEl.contains(event.target) || topbarEl.isSameNode(event.target) || topbarEl.contains(event.target));
};
let display = ref(false);
const open = async () => {
    try {
        const response = await axios.get(`${process.env.VUE_APP_HOST_BACKEND}/users/updates/latest/`);
        const responsedata = response.data;

        latestUpdate.value = responsedata.description;
        display.value = true;
      
    }catch (error) {
        console.error(error);
    }
};
let latestUpdate = ref(null);
const checkForUpdate= async () => {
    try {
        const response = await axios.get(`${process.env.VUE_APP_HOST_BACKEND}/users/updates/latest/`);

        if  ( (localStorage.getItem('Readupdate')=="false") || (response.data.version != localStorage.getItem('appversion')) )  {
            localStorage.setItem('appversion', response.data.version);
            latestUpdate.value = response.data.description;
            display.value = true;

        }else{
            display.value = false;  
        } 
    }catch (error) {
        console.error(error);
    }
};
const ConfirmUpdate = () => {
    localStorage.setItem('Readupdate', true);
    display.value = false;
};
const logoutClick = () => {
    localStorage.removeItem('tokenjwt');
    router.push('/');
};
onMounted(() => {
    bindOutsideClickListener();
    checkForUpdate()
});

onBeforeUnmount(() => {
    unbindOutsideClickListener();
});
</script>

<template>
   
    <div class="layout-topbar">

        <router-link to="/view/Dashboard" class="layout-topbar-logo">
            <span>IMMOBILIERE</span>
        </router-link>

        <button class="p-link layout-menu-button layout-topbar-button" @click="onMenuToggle()">
            <i class="fas fa-bars"></i>

        </button>

        <button class="p-link layout-topbar-menu-button layout-topbar-button" @click="onTopBarMenuButton()">
            <i class="fas fa-ellipsis-v"></i>
        </button>

        <div class="layout-topbar-menu" :class="topbarMenuClasses">
            <button  class="p-link layout-topbar-button"  @click="open" >
                <i class="fas fa-bell-on"></i>
                <span>Updates</span>
            </button>
            <button @click="onSettingsClick()" class="p-link layout-topbar-button">
                <i class="fas fa-user"></i>
                <span>Profile</span>
            </button>
            <button @click="logoutClick()" class="p-link layout-topbar-button">
                <i class="fas fa-sign-out"></i>
                <span>logout</span>
            </button>
        </div>
    </div>


    <Dialog header="Notitifications" v-model:visible="display" :breakpoints="{ '960px': '75vw' }" :style="{ width: '50vw' }" :modal="true">
        <p class="line-height-3 m-0">
            <h3>Une nouvelle mise à jour est disponible</h3>
            <h5>{{ latestUpdate }}</h5>
        </p>
        <template #footer>

            <Button  label="Confirmer " icon="pi pi-times" class="p-button-text"            @click="ConfirmUpdate()"/>

        </template>
    </Dialog>
</template>

<style lang="scss" scoped></style>
