const ReadDataPaiment = () => {
  maximumsteps.value=3
  
  nestedRouteItems.value=[{
    label: "Locataire",
    to: "",
  },
  {
    label: "Paiment",
    to: "",
  },
  {
    label: "Confirmation",
    to: "",
  }];
  display.value = true;
  stepone.value = true;
  steptwo.value = false;
  stepthree.value = false;
  updateprogressindex.value = 1;

  TypeCredidropdownValue.value = null;
  selectedLocataireaValue.value = null;
  CredidropdownValue.value = null;
  paymentmethoddropdownValue.value = null;
  chequeadd.num = null;
  chequeadd.echance = null;
  virement.num = null;
  virement.date = null;
  paymenttype.value = null;
  TypeCredidropdownValue.Value = null;
  errorMessage.value = false;
  espece.value.montant = null;

  setTimeout(() => {
    let liststeps = document.getElementsByClassName("p-steps-item");
    liststeps[0].classList.add("p-highlight");
    Appartmenttitledialog.value = "Chosir Locataire";
  }, 100);
  axios
    .get(`${process.env.VUE_APP_HOST_BACKEND}/payment/get/loccot`, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then((response) => {
      const creditData = [...response.data.creditC, ...response.data.creditL];
      creditLocataire.value = creditData;

      const locataires = {};
      creditData.forEach((item) => {
        const { idlocataire, fullname } = item;
        if (!(idlocataire in locataires)) {
          locataires[idlocataire] = fullname;
        }
      });
      const uniqueLocatairesArray = Object.entries(locataires).map(
        ([id, code]) => ({ id, code })
      );
      ExistantLocataire.value = uniqueLocatairesArray;
    })
    .catch((error) => {
      console.log(error);
    });
};
let resstegsonedevalues=ref([])
let resstegsonedevalue=ref()
const readstegsonede = () => {
  maximumsteps.value=4
  nestedRouteItems.value=[
    {
      label: "Locataire",
      to: "",
    },
    {
      label: "Compteur",
      to: "",
    },
    {
      label: "paiement",
      to: "",
    },
    {
      label: "Confirmation",
      to: "",
    },
  ];
  stegsonededialog.value = true;
  stepone.value = true;
  steptwo.value = false;
  stepthree.value = false;
  stepfour.value = false;
  updateprogressindex.value = 1;

  stegsonedevalue.value = null;
  selectedLocataireaValue.value = null;
  biendropdownvalue.value = null;
  espece.value.numfacture=null; 
  paymentmethoddropdownValue.value = null;
  chequeadd.num = null;
  chequeadd.echance = null;
  virement.num = null;
  virement.date = null;
  paymenttype.value = null;
  TypeCredidropdownValue.Value = null;
  espece.value.montant = null;
  paymentData.value={};

  setTimeout(() => {
    let liststeps = document.getElementsByClassName("p-steps-item");
    liststeps[0].classList.add("p-highlight");
    Appartmenttitledialog.value = "Chosir Locataire";
  }, 100);
  axios
    .get(`${process.env.VUE_APP_HOST_BACKEND}/payment/get/stegsonede`, {
      headers: {
        Authorization: tokenjwt,
      },
    })
    .then((response) => {

      stegsonedeData.value =response.data.data
      const responseData = response.data;

      if (Array.isArray(responseData.data)) {
        const stegsonedeData = responseData.data;

        const residencesSet = new Set();
        const residences = stegsonedeData.reduce((acc, item) => {
          if (item.resname && !residencesSet.has(item.resname)) {
            acc.push({
              id: item.resname,
              code: item.resname
            });
            residencesSet.add(item.resname);
          }
          return acc;
        }, []);

        resstegsonedevalues.value = residences;
      }
    })
    .catch((error) => {
      console.log(error);
    });
};
const getappartement = () => {
  const filteredData = stegsonedeData.value.filter(item => item.resname === resstegsonedevalue.value.id);

  const biendropdownValues = filteredData.map(item => {
    let etage;
    if (item.etagenum === "0") {
      etage = "RDC";
    } else {
      etage = item.etagenum;
    }

    let name;
    if (String(item.locationid).startsWith("APPF") ) {
      name =`${item.resname} Etage ${etage} Appartement Familliale ${item.numapp}`;
    } else if (String(item.locationid).startsWith("APPL")) {  
      name =`${item.resname} Etage ${etage} Appartement à Lit ${item.numapp}`;
    }
    else if (String(item.locationid).startsWith("LOC")) {
      name =`x${item.resname} Etage ${etage} Local Commercial ${item.IndexLocComm + 1}`;
    } 
    return {
      id: item.locationid,
      code: name,
      locataireid: item.Idlocataire,
      id: item.locationid,
      clientfullname: item.fullname,
      resname: item.resname,
      etagenum: item.etagenum,
      indexChambre: item.IndexChambre || null,
      indexLit: item.IndexLit,
      indexLocComm: item.IndexLocComm || null,
      resrue: item.resrue,
      lastcompteur:item.lastcompteur,
      chambre:item.chambre,
    };
  });

  biendropdownvalues.value = biendropdownValues;
};
let stegselected=ref(false)
let sonedeselected=ref(false)
let index=ref({})
const showindexes = () =>{
  if (stegsonedevalue.value.find(item => item.id === "ST")){
    stegselected.value=true
  }
  else{
    stegselected.value=false
  }
  if (stegsonedevalue.value.find(item => item.id === "SD")){
    sonedeselected.value=true
  }
  else{
