const { layoutConfig, layoutState, setActiveMenuItem, onMenuToggle } =
  useLayout();

const props = defineProps({
  item: {
    type: Object,
    default: () => ({}),
  },
  index: {
    type: Number,
    default: 0,
  },
  root: {
    type: Boolean,
    default: true,
  },
  parentItemKey: {
    type: String,
    default: null,
  },
});

const isActiveMenu = ref(false);
const itemKey = ref(null);

onBeforeMount(() => {
  itemKey.value = props.parentItemKey
    ? props.parentItemKey + "-" + props.index
    : String(props.index);

  const activeItem = layoutState.activeMenuItem;

  isActiveMenu.value =
    activeItem === itemKey.value || activeItem
      ? activeItem.startsWith(itemKey.value + "-")
      : false;
});

watch(
  () => layoutConfig.activeMenuItem.value,
  (newVal) => {
    isActiveMenu.value =
      newVal === itemKey.value || newVal.startsWith(itemKey.value + "-");
  }
  
);

const itemClick = (event, item) => {
  if (item.disabled) {
    event.preventDefault();
    return;
  }

  const { overlayMenuActive, staticMenuMobileActive } = layoutState;

  if (
    (item.to || item.url) &&
    (staticMenuMobileActive.value || overlayMenuActive.value)
  ) {
    onMenuToggle();
  }

  if (item.command) {
    item.command({ originalEvent: event, item: item });
  }

  const foundItemKey = item.items
    ? isActiveMenu.value
      ? props.parentItemKey
      : itemKey
    : itemKey.value;
  setActiveMenuItem(foundItemKey);
  
  if (item.label === "Location / Cautionnement") {
    ReadDataPaiment();
  } else if (item.label === "Steg / Sonede") {
    readstegsonede();
  }
  if (item.goto){
    setActiveMenuItem(foundItemKey);
    router.push(item.goto)
  }

};

const checkActiveRoute = (item) => {
  return route.path === item.to;
};
///////////////PAYMENT PART/////////////////////

const hidedialog = () => {
  display.value = false;
  stegsonededialog.value = false;
  updateprogressindex.value = 1;
  stepone.value = true;
  steptwo.value = false;
  stepthree.value = false;
};

let nextstep = () => {
  let liststeps = document.getElementsByClassName("p-steps-item");
  if (updateprogressindex.value >= maximumsteps.value) {
    updateprogressindex.value = maximumsteps.value;
    liststeps[2].classList.add("p-highlight");
    liststeps[1].classList.remove("p-highlight");
    liststeps[0].classList.remove("p-highlight");
  } else {
    for (let i = 0; i < liststeps.length; i++) {
      if (i == updateprogressindex.value) {
        liststeps[i].classList.add("p-highlight");
      } else {
        liststeps[i].classList.remove("p-highlight");
      }
    }
  }

  updateprogressindex.value++;
  switch(maximumsteps.value){
    case 4: 
      switch (updateprogressindex.value) {
        case 1 :
          setStep("Chosir Locataire", true, false, false, false);
          break;
        case 2 :
          setStep("Compteur ", false, true, false,false);
          break;
        case 3 :
          setStep("Paiment ", false, false, true, false);
          break;
        case 4 :
          setStep("Confirmation de Paiment", false, false, false, true);
          break;
      }
      break;
    case 3:
      switch(updateprogressindex.value){
        case 1 :
          setStep("Chosir Locataire", true, false, false,false);
          break;
        case 2 :
          setStep("Paiment ", false, true, false,false);
          break;
        case 3 :
          setStep("Confirmation de Paiment", false, false, true,false);
          break;
      }
      break;
  }
  
};
let previousstep = () => {
  updateprogressindex.value--;

  let liststeps = document.getElementsByClassName("p-steps-item");
  if (updateprogressindex.value <= 1) {
    updateprogressindex.value = 1;
    liststeps[0].classList.add("p-highlight");
    liststeps[1].classList.remove("p-highlight");
    liststeps[2].classList.remove("p-highlight");
  } else {
    for (let i = 0; i < liststeps.length; i++) {
      if (i + 1 === updateprogressindex.value) {
        liststeps[i].classList.add("p-highlight");
      } else {
        liststeps[i].classList.remove("p-highlight");
      }
    }
  }
  switch(maximumsteps.value){
    case 4: 
      switch (updateprogressindex.value) {
        case 1 :
          setStep("Chosir Locataire", true, false, false, false);
          break;
        case 2 :
          setStep("Compteur ", false, true, false,false);
          break;
        case 3 :
          setStep("Paiment ", false, false, true, false);
          break;
        case 4 :
          setStep("Confirmation de Paiment", false, false, false, true);
          break;
      }
      break;
    case 3:
      switch(updateprogressindex.value){
        case 1 :
          setStep("Chosir Locataire", true, false, false,false);
          break;
        case 2 :
          setStep("Paiment ", false, true, false,false);
          break;
        case 3 :
          setStep("Confirmation de Paiment", false, false, true,false);
          break;
      }
      break;
  }
  

};
let setStep = (title, stepOne, stepTwo, stepThree,stepFour) => {
  Appartmenttitledialog.value = title;
  stepone.value = stepOne;
  steptwo.value = stepTwo;
  stepthree.value = stepThree;
  stepfour.value = stepFour;

};

const searchLocataire = (event) => {
  setTimeout(() => {
    if (!event.query.trim().length) {
      autoFilteredValue.value = [...ExistantLocataire.value];
    } else {
      autoFilteredValue.value = ExistantLocataire.value.filter((filter) => {
        return filter.code.toLowerCase().startsWith(event.query.toLowerCase());
      });
    }
  }, 50);
};
const changeselectboxLC = () => {
  if (
    selectedLocataireaValue.value !== null &&
    ["C", "L"].includes(TypeCredidropdownValue.value.id)
  ) {
    const selectedCredits = creditLocataire.value.filter(
      (credit) =>
        credit.idlocataire === selectedLocataireaValue.value.id &&
        credit.credit.typecredit === TypeCredidropdownValue.value.id
    );

    let arraycredits = [];
    selectedCredits.forEach((creditloop) => {
      let date = new Date(creditloop.credit.datecredit);
      let name = "";
      let etage = "";
      if (creditloop.aboutbien.etagenum == "0") {
        etage = "RDC";
      } else {
        etage = creditloop.aboutbien.etagenum;
      }

      if (creditloop.credit.idlocation.startsWith("APPF")) {
        name =
          getMonthName(date.getMonth()) +
          " " +
          date.getFullYear() +
          `: Res. ${creditloop.aboutbien.resname} Etage ${etage} Appartement ${creditloop.aboutbien.numapp}`;
      } else if (creditloop.credit.idlocation.startsWith("LOC")) {
        name =
          getMonthName(date.getMonth()) +
          " " +
          date.getFullYear() +
          `: Res. ${
            creditloop.aboutbien.resname
          } Etage ${etage} Local Commercial ${
            creditloop.aboutbien.IndexLocComm + 1
          }`;
      } else if (creditloop.credit.idlocation.startsWith("APPL")) {
        name =
          getMonthName(date.getMonth()) +
          " " +
          date.getFullYear() +
          `: Res. ${
            creditloop.aboutbien.resname
          } Etage ${etage} Appartement Num: ${
            creditloop.aboutbien.numapp
          } CH: ${creditloop.aboutbien.IndexChambre + 1} lit ${
            creditloop.aboutbien.IndexLit + 1
          }`;
      }

      arraycredits.push({
        idcredit: creditloop.credit.Id,
        locatiaredata: selectedLocataireaValue.value,
        typecredit: TypeCredidropdownValue.value.id,
        montantcredit: creditloop.credit.montant,
        id: creditloop.credit.idlocation,
        name: name,
        monthfor: date,

        resname: creditloop.aboutbien.resname,
        resrue: creditloop.aboutbien.resrue,

        nsteg: creditloop.aboutbien.nsteg,
        nsonede: creditloop.aboutbien.nsonede,
        ngaz: creditloop.aboutbien.ngaz,

        etagenum: creditloop.aboutbien.etagenum,
        indexLit: creditloop.aboutbien.IndexLit,
        indexChambre: creditloop.aboutbien.IndexChambre || null,
        indexLocComm: creditloop.aboutbien.IndexLocComm || null,
        numapp: creditloop.aboutbien.numapp || null,

        from: creditloop.aboutbien.from,
        to: creditloop.aboutbien.to,
      });
    });

    CredidropdownValues.value = arraycredits;
  } else {
    CredidropdownValues.value = [];
  }
};
let biendropdownvalues = ref([]);
let biendropdownvalue = ref(null);

const changepaymentmethod = () => {
  if (paymentmethoddropdownValue.value.id == "CH") {
    paymenttype.value = 1;
  } else if (paymentmethoddropdownValue.value.id == "ES") {
    paymenttype.value = 2;
  } else if (paymentmethoddropdownValue.value.id == "VR") {
    paymenttype.value = 3;
  }
};
let checksteptwo = () => {
  if (espece.value.montant > CredidropdownValue.value.montantcredit) {
    errorMessage.value = true;
  } else {
    nextstep();
    errorMessage.value = false;
  }
};
let validateInput = () => {
  espece.value.montant = espece.value.montant.replace(/\D/g, "");
};

const onUpload = async (e, type) => {
  selectedFiles = e.files;
  if (!selectedFiles || selectedFiles.length === 0) {
    return;
  }
  const formData = new FormData();
  for (let i = 0; i < selectedFiles.length; i++) {
    formData.append("files", selectedFiles[i]);
  }
  formData.append("counter", selectedFiles.length);
  formData.append("clientId", selectedLocataireaValue.value.id);
  formData.append("LocationId", CredidropdownValue.value.id);
  formData.append("paymenttype", type);
  await axios
    .post(
      `${process.env.VUE_APP_HOST_BACKEND}/upload/justificatif/${type}`,
      formData
    )
    .then((response) => {
      uploadedFiles.value = true;
      toast.add({
        severity: "success",
        summary: "Action Terminée",
        detail: "Fichier Ajoutée",
        life: 5000,
      });

      paymentData.value.filesnames = response.data.files;
    })
    .catch((error) => {
      console.log(error);
      uploadedFiles.value = false;
    });
};
const onUploadSTSD = async (e) => {
  selectedFiles = e.files;
  if (!selectedFiles || selectedFiles.length === 0) {
    return;
  }
  const formData = new FormData();
  for (let i = 0; i < selectedFiles.length; i++) {
    formData.append("files", selectedFiles[i]);
  }
  formData.append("counter", selectedFiles.length);
  formData.append("compteurid",biendropdownvalue.value.lastcompteur._id)
  formData.append("LocationId",biendropdownvalue.value.id||biendropdownvalue.value.id[0])
  

  let type=paymenttype.value;
  await axios
    .post(
      `${process.env.VUE_APP_HOST_BACKEND}/upload/justificatif/${type}`,
      formData
    )
    .then((response) => {
      uploadedFiles.value = true;
      toast.add({
        severity: "success",
        summary: "Action Terminée",
        detail: "Fichier Ajoutée",
        life: 5000,
      });
      console.log(response.data.files)
      // paymentData.value.filesnames = response.data.files;
    })
    .catch((error) => {
      console.log(error);
      uploadedFiles.value = false;
    });
};
const generatepdf = () => {
  isLoading.value=true;
  hidedialog();

  let quitID = parseInt(Date.now() / 100);
  let fromdate =
    new Date(CredidropdownValues.value[0].from).getFullYear() +
    "-" +
    new Date(CredidropdownValues.value[0].from).getMonth() +
    "-" +
    new Date(CredidropdownValues.value[0].from).getDate();
  let todate =
    new Date(CredidropdownValues.value[0].to).getFullYear() +
    "-" +
    new Date(CredidropdownValues.value[0].to).getMonth() +
    "-" +
    new Date(CredidropdownValues.value[0].to).getDate();
  let monthfor =
    new Date(CredidropdownValues.value[0].monthfor).getFullYear() +
    "-" +
    new Date(CredidropdownValues.value[0].monthfor).getMonth();
  let datepay =
    new Date(Date.now()).getFullYear() +
    "-" +
    new Date(Date.now()).getMonth() +
    "-" +
    new Date(Date.now()).getDate();
  let body = {
    numquit: quitID,

    ImmName: CredidropdownValues.value[0].resname,
    rue: CredidropdownValues.value[0].resrue,

    etage: CredidropdownValues.value[0].etagenum,
    appart: CredidropdownValues.value[0].numapp,
    idcredit: CredidropdownValues.value[0].idcredit,
    idlocataire: CredidropdownValues.value[0].locatiaredata.id,
    idlocation: CredidropdownValues.value[0].id,
    Type: CredidropdownValues.value[0].typecredit,

    fullname: CredidropdownValues.value[0].locatiaredata.code,

    chambre:
      CredidropdownValues.value[0].indexChambre !== null
        ? CredidropdownValues.value[0].indexChambre + 1
        : null,
    localCom:
      CredidropdownValues.value[0].indexLocComm !== null
        ? CredidropdownValues.value[0].indexLocComm + 1
        : null,
    lit:
      CredidropdownValues.value[0].indexLit !== null
        ? CredidropdownValues.value[0].indexLit + 1
        : null,

    nsteg: CredidropdownValues.value[0].nsteg,
    nsonede: CredidropdownValues.value[0].nsonede,

    montant: espece.value.montant,
    creditreste: CredidropdownValue.value.montantcredit - espece.value.montant,
    typemontant: paymenttype.value,
    month: monthfor,

    datedeb: fromdate,
    datefin: todate,
    datepay: datepay,

    refchequevir: chequeadd.value.num || virement.value.num,
    datechequevir: chequeadd.value.echance || virement.value.date,

    filenames: paymentData.value.filesnames,
  };
  axios
    .post(`${process.env.VUE_APP_HOST_BACKEND}/payment/post/SetPayment`, body, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
      responseType: "blob",
    })
    .then((response) => {

      setTimeout(() => {
          isLoading.value=false;
            toast.add({
              severity: "success",
              summary: "Action Terminée",
              detail: "Paiment Confirmée",
              life: 10000,
            });
        }, 2000);
      const blob = new Blob([response.data], { type: "application/pdf" });
      const url = URL.createObjectURL(blob);

      const newWindow = window.open(url, "_blank");
      newWindow.print();
    })
    .catch((error) => {
      console.error("Error generating receipt:", error);
    });
};
//////////////////////////////STEG SONEDE PART
