<script setup>
import { ref } from 'vue';

const email = ref('');
const password = ref('');


</script>

<template>
    <div class="surface-ground flex align-items-center justify-content-center min-h-screen min-w-screen overflow-hidden">
        <div class="flex flex-column align-items-center justify-content-center">
            <div style="border-radius: 56px; padding: 0.3rem; background: linear-gradient(180deg, var(--primary-color) 10%, rgba(33, 150, 243, 0) 30%)">
                <div class="w-full surface-card py-8 px-5 sm:px-8" style="border-radius: 53px">
                    <div class="text-center mb-5">
                        <div class="text-900 text-3xl font-medium mb-3">Bonjour, Mohamed !</div>
                        <span class="text-600 font-medium">Connectez-vous pour continuer</span>
                    </div>

                    <div>
                        <form>
                            <label for="email1" class="block text-900 text-xl font-medium mb-2">Email</label>
                            <InputText id="email1" type="text" placeholder="Email address" class="w-full md:w-30rem mb-5" style="padding: 1rem" v-model="form.email" />

                            <label for="password1" class="block text-900 font-medium text-xl mb-2">mot de passe</label>
                            <InputText id="password1" type="password" placeholder="Password" class="w-full md:w-30rem mb-5" style="padding: 1rem" v-model="form.password" />

                            <div class="flex align-items-center justify-content-between mb-5 gap-5"></div>
                            <Button :disabled="!form.email || !form.password" label="Sign In" class="w-full p-3 text-xl" @click.prevent="submitForm" ></Button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            form: {
                email: '',
                password: '',
                title: process.env.NODE

            }
        };
    },
	methods: {
		
		async submitForm() {

			axios.post(`${process.env.VUE_APP_HOST_BACKEND}/users/login`, {
				username: this.form.email,
				password: this.form.password
			})
			.then(response => {
                console.log (response)
				if (response.data.success){

					localStorage.setItem('token', response.data.token); // Store token in local storage
                    localStorage.setItem('adminlvl', response.data.adminlvl); // Store token in local storage

                    this.$router.push({ name: 'Dashboard' });
				}
				else{
					alert(response.data.msg)
				}
			})
			.catch(error => {
				alert('Wrong user account ' +error )
			});

		},
	},
}
</script>
